<template>
  <div class="p5 noUserSelect">
    <Header heading="Processing JS" />
    <P5NavBar />
    <div ref="canvas" class="canvasP5"></div>
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import P5NavBar from "@/components/P5NavBar.vue";

export default {
  components: {
    Header,
    P5NavBar
  },
  data() {
    return {
      script: null,
      ps: null,
      canvas: null
    };
  },
  mounted() {
    this.script = p => {
      p.setup = () => {
        this.canvas = p.createCanvas(p.windowWidth, p.windowHeight, p.WEBGL);
        this.canvas.parent(this.$refs.canvas);
        p.frameRate(30);
      };

      p.draw = () => {
        p.background(255);
        p.stroke("#42b983");
        p.strokeWeight(2);
        p.noFill();
        p.orbitControl();
        p.rotateX(p.radians(-25));
        p.rotateY(p.frameCount * 0.01);
        p.box(p.width / 6);
      };

      p.windowResized = () => {
        p.resizeCanvas(p.windowWidth, p.windowHeight);
      };
    };
    const P5 = require("p5");
    this.ps = new P5(this.script);
    // eslint-disable-next-line
    console.log(this.ps);
  }
};
</script>
